//

import React, { Component } from "react";
import "./practice.css";
import Courses from "./courses";
import course_data from "./data";
import { Scrollbars } from "react-custom-scrollbars";

class Filter extends Component {
  //
  all = course_data;
  constructor() {
    super();
    this.state = { count: 0, courses: this.get_data(0) };
    this.filter_all = this.filter_all.bind(this);
    this.update_count = this.update_count.bind(this);
  }
  filter_by_language(data) {
    // console.log(data);
    var flag1 = document.getElementById("lang1");
    var flag2 = document.getElementById("lang2");
    var both = flag1.checked && flag2.checked;
    if (flag1.checked && !both) {
      const arr = [];
      for (let i in data) {
        if (data[i].language === "english") {
          arr.push(data[i]);
        }
      }
      return arr;
    } else if (flag2.checked && !both) {
      const arr = [];
      for (let i in data) {
        if (data[i].language !== "english") {
          arr.push(data[i]);
        }
      }
      return arr;
    } else {
      return data;
    }
  }

  filter_by_category(data) {
    var flag1 = document.getElementById("type1");
    var flag2 = document.getElementById("type2");
    var both = flag1.checked && flag2.checked;
    if (flag1.checked && !both) {
      const arr = [];
      for (let i in data) {
        if (data[i].type === "diploma") {
          arr.push(data[i]);
        }
      }
      return arr;
    } else if (flag2.checked && !both) {
      const arr = [];
      for (let i in data) {
        if (data[i].type === "certificate") {
          arr.push(data[i]);
        }
      }
      return arr;
    } else {
      return data;
    }
  }

  filter_by_level(data) {
    var flag1 = document.getElementById("level1");
    var flag2 = document.getElementById("level2");
    var flag3 = document.getElementById("level3");

    var no_checked =
      flag1.checked === false &&
      flag2.checked === false &&
      flag3.checked === false;

    if (no_checked) {
      return data;
    } else {
      const arr = [];

      if (flag1.checked) {
        for (let i in data) {
          if (data[i].level === "beginner") {
            arr.push(data[i]);
          }
        }
      }
      if (flag2.checked) {
        for (let i in data) {
          if (data[i].level === "intermediate") {
            arr.push(data[i]);
          }
        }
      }
      if (flag3.checked) {
        for (let i in data) {
          if (data[i].level === "hard") {
            arr.push(data[i]);
          }
        }
      }
      return arr;
    }
  }

  get_data(count) {
    var index = count * 3;
    var result = course_data.slice(index, index + 3);
    return result;
  }

  filter_all() {
    var count = this.state.count;
    var data = this.get_data(count);
    console.log(data);
    var level = this.filter_by_level(data);
    var language = this.filter_by_language(data);
    var category = this.filter_by_category(data);

    var arr = [];

    for (var i = 0; i < level.length; i++) {
      for (var j = 0; j < language.length; j++) {
        if (level[i].id == language[j].id) {
          arr.push(this.all[level[i].id - 1]);
        }
      }
    }

    var temp = [];

    for (var i = 0; i < arr.length; i++) {
      for (var j = 0; j < category.length; j++) {
        if (arr[i].id == category[j].id) {
          temp.push(this.all[arr[i].id - 1]);
        }
      }
    }

    this.setState({ courses: temp });
  }

  update_count(event) {
    var id = event.target.id;
    var limit = Math.floor(this.all.length / 3);
    if (id === "prev" && this.state.count !== 0) {
      this.state.count -= 1;
    } else if (id === "next" && this.state.count !== limit) {
      this.state.count += 1;
    }
    // console.log(this.state.count);
    this.filter_all();
  }

  render() {
    return (
      <div className="page">
        <div id="container">
          <Scrollbars style={{ height: "450px" }}>
            <h3 style={{ color: "white" }}>Refine your Search</h3>

            <h4 className="filter-types">Course Language</h4>
            <>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="lang1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="lang1"
                >
                  English
                </label>
              </div>

              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="lang2"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="lang2"
                >
                  Non-English
                </label>
              </div>
            </>
            <h4 className="filter-types">Course Level</h4>
            <>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="level1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="level1"
                >
                  Beginner
                </label>
              </div>

              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="level2"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="level2"
                >
                  Intermediate
                </label>
              </div>

              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="level3"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="level3"
                >
                  Hard
                </label>
              </div>
            </>
            <h4 className="filter-types">Course Type</h4>
            <>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="type1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="type1"
                >
                  Diploma
                </label>
              </div>

              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="type2"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="type2"
                >
                  Certification
                </label>
              </div>
            </>
            <h4 className="filter-types">Dummy</h4>
            <>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="dum1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="dum1"
                >
                  Dummy
                </label>
              </div>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="dum1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="dum1"
                >
                  Dummy
                </label>
              </div>
              <div className="checks">
                <input
                  onChange={this.filter_all}
                  id="dum1"
                  type="checkbox"
                  className="checkboxes"
                ></input>
                <label
                  style={{ margin: "5px" }}
                  className="filter-labels"
                  for="dum1"
                >
                  Dummy
                </label>
              </div>
            </>
          </Scrollbars>
        </div>
        <div className="courses-display">
          <span>
            <h2> &nbsp; Top Courses</h2>
          </span>
          <Courses courses={this.state.courses} />
        </div>
        <div className="pagination">
          <span id="prev" onClick={this.update_count}>
            Previous
          </span>
          <span id="next" onClick={this.update_count}>
            Next
          </span>
        </div>
      </div>
    );
  }
}

export default Filter;
